import React, {Component} from "react";
import {Container, Divider, Icon, Image, Grid, Menu} from "semantic-ui-react";

import Posikyi from './posikyi-screenshot.png';
import POPOHome from './popo-home.png';
import POPOAdmin from './popo-admin.png';
import FaceDetectionFlow from './face-detection-flow.jpg';
import FaceDetectionExample from './face-detection-example.gif';
import CTCamp1 from './ct-camp-1.jpg';
import CTCamp2 from './ct-camp-2.jpg';

class Project extends Component {
  render() {
    return (
      <Container style={{backgroundColor: "white", color: "black", padding: "1em 5em"}}>
        <h1><a href="/">Home</a> / Project</h1>

        <Container style={{margin: "10px 0", textAlign: "center"}}>
          <h1 style={{fontFamily: "caveat"}}>Backend + DL = ✨Magic✨</h1>
        </Container>
        <Divider/>
        <Container>
          <h2>포식이, 포스텍 모바일 식권 서비스</h2>
          <p>
            &nbsp;&nbsp;&nbsp;&nbsp; 포식이는 '포스텍(POSTECH)' + '식사'를 합친 단어로, 대학 맞춤형 학식 통합 결제 시스템 입니다😋
            기존 학생 정식 식권 모바일화 뿐만 아니라, 밀플랜, 식단표, 도시락 주문 등 포스텍 복지회에서 서비스하는 다양한 기능들을 포함하고 있습니다.
            저는 CBO <small>Chief Backend Officer</small>로서 "NestJS" 프레임워크를 이용해 DB를 구성하고 API를 구축하였습니다.&nbsp;
            <a href="https://play.google.com/store/apps/details?id=com.poapper.posikyi" target='_blank'>
              <Icon name="google play"/>
            </a>
            <a href="https://apps.apple.com/kr/app/id1548629051" target='_blank'>
              <Icon name="apple"/>
            </a>
            <br/>
            * 현재 포스텍 지곡회관 해동 아우름홀 및 위즈덤에서 시범 운영 중입니다!
            <br/>
          </p>
          <div>
            <Image src={Posikyi} centered size='large'/>
          </div>
        </Container>
        <Divider/>
        <Container>
          <h2>
            POPO, POstechian's POrtal &nbsp;
            <small><a href="http://popo.postech.ac.kr/"><Icon link name={'linkify'}/></a></small>
          </h2>
          <p>
            &nbsp;&nbsp;&nbsp;&nbsp; 포포(POPO)는 포스텍 총학생회에서 관리 및 제공하고는 장비/장소를 예약할 수 있는 편의 서비스입니다.
            포스텍 총학생회에서 운영하고 포애퍼가 유지/보수하며, 편리한 가입, 손쉬운 예약 서비스, 학생단체, 동아리 정보 등을 확인할 수 있는 사이트입니다.
            별도의 관리자 페이지가 있으며, 관리자 페이지에서 제공하는 사이트 이용 통계를 통해 총학생회 사업에 이바지 하고 있습니다. &nbsp;
            <a href="https://github.com/PoApper/POPO-nest-api" target='_blank'><Icon link name={'github'}/></a>
          </p>
          <div>
            <Grid columns={2}>
              <Grid.Column>
                <Image src={POPOHome} centered size='large'/>
              </Grid.Column>
              <Grid.Column>
                <Image src={POPOAdmin} centered size='large'/>
              </Grid.Column>
            </Grid>
          </div>
        </Container>
        <Divider/>
        <Container>
          <h2>
            Face Detection with GoPro360
          </h2>
          <p>
            &nbsp;&nbsp;&nbsp;&nbsp; 한국과학기술연구원(KIST)에서 SES 인턴십 기간동안 진행한 프로젝트입니다.
            360도 촬영이 가능한 GoPro Fusion으로 실시간 영상을 받아 Face Detection을 수행하는 연구를 진행했습니다.
            Python과 numpy, TensorFlow를 이용해 구현했습니다.
          </p>
          <div>
            <Grid columns={2}>
              <Grid.Column>
                <Image src={FaceDetectionFlow} centered size='large'/>
              </Grid.Column>
              <Grid.Column>
                <Image src={FaceDetectionExample} centered size='large'/>
              </Grid.Column>
            </Grid>
          </div>
        </Container>
        <Divider/>
        <Container>
          <h2>
            Guessing the strategy of two player Tit-for-Tat game
          </h2>
          <p>
            &nbsp;&nbsp;&nbsp;&nbsp; 2020년 겨울에 진행된 CT 캠프에 게임 개발팀으로 참가하여 개발한 게임입니다.
            플레이어는 10번의 도전 기회를 가지며, 10번의 게임 동안 프로그래밍된 오토마타를 통해 두 명의 보스와 Tit-for-Tat 게임을 진행합니다.
            플레이어는 보스와의 게임 결과를 통해 보스의 오토마타를 유추하고, 게임에서 가장 큰 점수를 얻을 수 있는 오토마타를 프로그래밍해야 합니다.
            Java와 Swing UI를 통해 구현했습니다.
          </p>
          <div>
            <Grid columns={2}>
              <Grid.Column>
                <Image src={CTCamp1} centered size='large'/>
              </Grid.Column>
              <Grid.Column>
                <Image src={CTCamp2} centered size='medium'/>
              </Grid.Column>
            </Grid>
          </div>
        </Container>
        <br/>
      </Container>
    )
  }

}

export default Project;