import React, {Component} from "react";
import {Container, Image, List, Menu} from "semantic-ui-react";
import logo from "../../logo.png";
import './home.css';
import {Link} from "react-router-dom";

class Home extends Component {

  render() {
    return (
      <div stlye={{textAlign: "center"}}>
        <header className="App">
          <div style={{marginBottom: "8px"}}>
            <Image className={"shadowed-white"} src={logo} alt="logo"/>
          </div>
          <div style={{padding: "8px", textAlign: "center", fontSize: "22px"}}>
            <p style={{margin: 0}}><big>하석윤</big> Ha Seokyun</p>
            <p><small>@BlueHorn07 @bluehornblues</small></p>
          </div>
          <p style={{margin: "10px 0", textAlign: "center", fontSize: "22px"}}>
            Backend👾 + DL🤖 = ✨Magic✨
          </p>
          <Menu style={{margin: "1em 0 0"}} size='large'>
            <Menu.Item header>🎷</Menu.Item>
            <Link to='/about'>
              <Menu.Item as='a'>About</Menu.Item>
            </Link>
            <Link to='/project'>
              <Menu.Item as='a'>Projects</Menu.Item>
            </Link>
            <Link to='/toy'>
              <Menu.Item as='a'>Toys</Menu.Item>
            </Link>
            <Menu.Item disabled>CV</Menu.Item>
            <Menu.Item header>🎺</Menu.Item>
          </Menu>
          <List horizontal divided link size='small' style={{color: "white"}}>
            <List.Item as='a' href='https://github.com/BlueHorn07'>
              GitHub
            </List.Item>
            <List.Item as='a' href='https://www.facebook.com/profile.php?id=100005615439995'>
              Facebook
            </List.Item>
            <List.Item as='a' href='https://www.acmicpc.net/user/bluehorn07'>
              BaekJoon
            </List.Item>
          </List>
        </header>
      </div>
    );
  }
}

export default Home;